@import "../../style/variables.scss";

.app__banner {
  margin-top: 50px;
  background-color: $dark;
  max-height: 100px;
  display: grid;
  grid-template-columns: 152px auto 133px;
  padding: 0 25px 0 45px;
  &-text {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    display: flex;
    align-items: center;
    margin-left: 83px;

    color: #ffffff;
  }
}
